<template>
  <div class="phone-viewport">
    <md-bottom-bar md-type="shift" :md-theme="'bottom-bar-' + value.theme">
      <md-bottom-bar-item md-label="首页" md-icon="home" @click="setTheme('blue', '')"></md-bottom-bar-item>
      <md-bottom-bar-item id="item-posts" @click="setTheme('cyan', 'order')">
        <md-icon class="md-bottom-bar-icon">shopping_cart</md-icon>
        <span class="md-bottom-bar-label">购物车</span>
        <i class="badge" v-if="$store.state.orders.len">{{ $store.state.orders.len }}</i>
      </md-bottom-bar-item>
      <md-bottom-bar-item md-label="收藏" md-icon="favorite" @click="setTheme('red', 'favorite')">
        <i class="badge" v-if="$store.state.favorites.len">{{ $store.state.favorites.len }}</i>
      </md-bottom-bar-item>
      <md-bottom-bar-item md-label="我的" md-icon="account_circle" @click="setTheme('green', 'admin')"></md-bottom-bar-item>
    </md-bottom-bar>
  </div>
</template>

<script>
export default {
  props: ['value'],
  methods: {
    setTheme(theme, path) {
      if (path === 'order') {
        this.$store.dispatch('setOrder', 0)
      } else if (path === 'favorite') {
        this.$store.dispatch('setFavorite', 0)
      }
      this.$router.push(`/${path}`)
      this.$emit('input', { theme })
    }
  }
}
</script>


