<template>
  <div class="page-container home">
    <md-content class="card-expansion">
      <md-card @click.native="$router.push(`/detail/${item.id}`)" class="md-card-food" v-for="item in items" :key="item.id">
        <md-ripple>
          <md-card-header>
            <md-card-media>
              <img :src="`/static/foods/${item.src}`" :alt="item.title">
            </md-card-media>
            <md-card-header-text>
              <div class="md-title">{{item.title}}</div>
              <div class="md-price">{{item.price | price}}</div>
              <div class="md-subhead">{{item.price | cn}}</div>
            </md-card-header-text>
          </md-card-header>
        </md-ripple>
      </md-card>
    </md-content>
  </div>
</template>

<script>
export default {
  data: () => ({
    items: [{
      id: 1,
      src: '1.png',
      title: '炸鸡腿',
      price: 19000
    },
    {
      id: 2,
      src: '2.png',
      title: '皮渣',
      price: 23000
    },
    {
      id: 3,
      src: '3.png',
      title: '猪排',
      price: 16000
    },
    {
      id: 4,
      src: '4.png',
      title: '炸鸡腿',
      price: 18000
    },
    {
      id: 5,
      src: '5.jpg',
      title: '炸鸡腿',
      price: 17000
    }]
  }),
  filters: {
    price(price) {
      return `${String(price).replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1,')} 韩元`
    },
    cn(price) {
      return `约${Math.floor(price * 0.0059)}元`
    }
  }
}
</script>


